* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

#datetime-picker {
  border: 1px solid rgba(0, 123, 255, .93);
  border-radius: 5px;
  margin-right: 10px;
  padding: 10px;
  font-size: 16px;
}

.timer {
  justify-content: center;
  align-items: center;
  margin-top: 20px;
  font-size: 32px;
  display: flex;
}

.field {
  flex-direction: column;
  align-items: center;
  margin: 0 10px;
  display: flex;
}

.value {
  color: #007bff;
  margin-bottom: 10px;
  font-size: 48px;
  font-weight: bold;
}

.label {
  text-transform: uppercase;
  font-size: 16px;
}

.container {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container button {
  cursor: pointer;
  border: none;
  border-radius: .5rem;
  margin: 1rem;
  padding: 1rem 2rem;
  font-size: 2rem;
}

.container button:hover {
  background-color: #005ea8;
}

